<style lang="scss" scoped>
.main-body {
  padding: 3rem 0 4rem;
}

.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  line-height: 150px;
  text-align: center;
  background-color: #39a9ed;
}
</style>

<template>
  <div>
    <Header
      :displaySign='true'
      :displaySearch="true"
    ></Header>

    <section class="main-body">
      <!------- 轮播图 ------->
      <!-- #region  -->
      <van-swipe
        class="my-swipe"
        :autoplay="5000"
        indicator-color="white"
      >
        <van-swipe-item>1</van-swipe-item>
        <van-swipe-item>2</van-swipe-item>
        <van-swipe-item>3</van-swipe-item>
        <van-swipe-item>4</van-swipe-item>
      </van-swipe>
      <!-- #endregion  -->
      <!------- end ------->

      <!------- 选项 ------->
      <!-- #region  -->
      <van-grid square>
        <van-grid-item
          :icon="require(`@/${'/assets/images/Home/beverage_icon.png'}`)"
          text="饮料"
          to="/drinks"
        />
        <van-grid-item
          :icon="require(`@/${'/assets/images/Home/flavor_icon.png'}`)"
          text="炸卤小吃"
        />
        <van-grid-item
          :icon="require(`@/${'/assets/images/Home/noodle_icon.png'}`)"
          text="文字"
        />
        <van-grid-item
          :icon="require(`@/${'/assets/images/Home/hamburger_icon.png'}`)"
          text="文字"
        />
      </van-grid>
      <!-- #endregion  -->
      <!------- end ------->

      <cm-gap></cm-gap>
      <!-------  ------->
      <!-- #region  -->
      <div class="shop-wrap">
        <cm-sg-shop-info
          v-for="(item, index) in GetShopInfo"
          :key="index"
          :shopInfo="item"
        ></cm-sg-shop-info>
      </div>
      <!-- #endregion  -->
      <!------- end ------->
    </section>
  </div>
</template>

<script>
import { mapActions, mapGetters, mapMutations, mapState } from "vuex";

import Header from '../../components/Header'
import CmGap from '../../components/common/Gap'
import CmSgShopInfo from '../../components/common/SgShopInfo'

export default {
  name: 'Home',
  components: { Header, CmGap, CmSgShopInfo },
  computed: {
    ...mapGetters('Home', ['GetShopInfo'])
  },
  methods: {
    ...mapMutations('Home', ['InitShopInfo'])
  },

  mounted() {
    this.InitShopInfo();
  }
}
</script>

